.add-sale__form-wrapper, .form-wrapper {
    margin-bottom: 40px;
}

.add-sale__form, .form {
    display: flex;
    flex-direction: column;
    gap: 10px;

    fieldset {
        display: flex;
        gap: 50px;
    }
    legend {
        padding-bottom: 10px;
    }

    @media (max-width: 800px) {
        margin-bottom: 20px;
    }
}

.add-sale__form-radio-wrapper {
}

.add-sale__form-input, .form__input{
    border: 1px solid #dadce1;
    width: 100%;
    height: 36px;
    border-radius: 2px;
    padding: 0 10px;

    &::placeholder {
        font-size: 13px;
        font-weight: 300;
    }
}

[type='radio']:checked,
[type='radio']:not(:checked) {
    position: absolute;
    left: -9999px;
}
[type='radio']:checked + label,
[type='radio']:not(:checked) + label {
    position: relative;
    padding-left: 28px;
    cursor: pointer;
    line-height: 20px;
    display: inline-block;
    color: black;
}
[type='radio']:checked + label:before,
[type='radio']:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    border: 1px solid #ddd;
    border-radius: 100%;
    background: #fff;
}
[type='radio']:checked + label:after,
[type='radio']:not(:checked) + label:after {
    content: '';
    width: 10px;
    height: 10px;
    background: black;
    position: absolute;
    top: 4px;
    left: 4px;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
[type='radio']:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}
[type='radio']:checked + label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

.add-sale__form-btn, .form__btn {
    background-color: black;
    color: white;
    width: 130px;
    height: 40px;
    font-size: 15px;
    border-radius: 10px;
    padding: 8.5px 0;
    margin-top: 20px;

    @media (max-width: 800px) {
        height: 45px;
        width: 140px;
        font-size: 16px;
    }
}
