.add-sale {
    max-width: 400px;
    margin: 0 auto;
}

.add-btn {
    display: block;
    border: 1.5px solid #f94f0d;
    height: 40px;
    font-size: 15px;
    border-radius: 10px;
    padding: 8.5px 0;
    width: 130px;
    margin-bottom: 20px;
    @media (max-width: 800px) {
        height: 45px;
        width: 140px;
        font-size: 16px;
    }
}

.add-income-btn, .add-sale-btn {
    border: 1.5px solid #1b1b1b;
}

